import React from 'react'
import {
  Box,
  Stack,
  Button,
  Typography,
  Divider,
  Fade
} from '@mui/material'
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import useModal from '../hooks/useModal';
import FilteringModal from '../components/FilteringModal';
import JobPostList from '../components/JobPostList';
import { useSearchParams } from 'react-router-dom';
import JobLabel from '../constant/JobLabel';

const JobPost = () => {

  const { open, handleOpen, handleClose } = useModal()
  const [searchParams,] = useSearchParams()

  const job = searchParams.get('job')
  const techStack = searchParams.get('tech-stack')
  const region = searchParams.get('region')
  const salary = searchParams.get('salary')
  const career = searchParams.get('career')
  const isView = Boolean(techStack || region || salary || career)

  return (
    <Fade in={true}>


      <Stack marginTop={'2rem'} spacing={2}>
        <Button
          endIcon={<ArrowDropDownIcon></ArrowDropDownIcon>}
          color='white'
          onClick={handleOpen}
          sx={{
            "&:hover": {
              backgroundColor: 'rgba(0,0,0,0)'
            },
            borderRadius: '25px',
            fontWeight: '700',
            width: '100%',
            margin: 'auto'
          }}
        >
          <Typography variant={searchParams.size === 0 ? 'h3' : 'h4'}>
             ,     ,     
          </Typography>
        </Button>
        <FilteringModal open={open} handleClose={handleClose}></FilteringModal>
        {isView && <Divider color={'white'} ></Divider>}
        {isView && <Box sx={{ color: '#ffffff' }}>
          {job && <Typography variant='body1'>  : {JobLabel[job]['kr']}</Typography>}
          {techStack && <Typography variant='body1'>  : {techStack.split(',').join(', ')}</Typography>}
          {region && <Typography variant='body1'>  : {region.split(',').join(', ')}</Typography>}
          {salary && <Typography variant='body1'>  : {salary} </Typography>}
          {career && <Typography variant='body1'>  : {career} </Typography>}
        </Box>}

        <Divider color={'white'} ></Divider>
        <JobPostList></JobPostList>
      </Stack>
    </Fade>
  )
}

export default JobPost